import config from "../config/index";
import {
    makeAxiosGetRequest,
    makeAxiosPostRequest
} from "./http-service";

export const getSettings = (params) => {
    return new Promise((resolve, reject) => {
        makeAxiosGetRequest(`${config.baseUrl}get-settings`, true, params)
            .then(res => {
                resolve(res.data);
            })
            .catch(e => {
                
                reject(e.response);
            });
    });
};

export const updateSettings = (params) => {
    return new Promise((resolve, reject) => {
        makeAxiosPostRequest(`${config.baseUrl}update-settings`, true, params)
            .then(res => {
                resolve(res.data);
            })
            .catch(e => {
                reject(e.response);
            });
    });
};


